<template>
  <div class="min-h-screen bg-gray-50">
    <!-- Profile Header -->
    <div class="bg-white shadow">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div class="flex items-center space-x-8 py-6">
          <!-- User Avatar -->
          <div class="flex-shrink-0">
            <img 
              :src="authStore.user?.avatar || defaultAvatar" 
              :alt="authStore.user?.fullName"
              class="h-20 w-20 rounded-full object-cover border-4 border-blue-500"
            />
          </div>
          
          <!-- User Info -->
          <div class="flex-1">
            <h1 class="text-2xl font-bold text-gray-900">
              {{ authStore.user?.fullName }}
            </h1>
            <p class="text-gray-600">{{ authStore.user?.email }}</p>
            <div class="mt-2 flex items-center space-x-4 text-sm text-gray-500">
              <span class="flex items-center">
                <span class="w-2 h-2 bg-green-400 rounded-full mr-2"></span>
                Активен
              </span>
              <span>Участник с {{ formatDate(authStore.user?.createdAt) }}</span>
            </div>
          </div>
          
          <!-- Quick Stats -->
          <div class="hidden md:flex space-x-8">
            <div class="text-center">
              <div class="text-2xl font-bold text-blue-600">{{ learningStats?.totalCoursesEnrolled || 0 }}</div>
              <div class="text-sm text-gray-500">Курсов</div>
            </div>
            <div class="text-center">
              <div class="text-2xl font-bold text-green-600">{{ learningStats?.totalCoursesCompleted || 0 }}</div>
              <div class="text-sm text-gray-500">Завершено</div>
            </div>
            <div class="text-center">
              <div class="text-2xl font-bold text-purple-600">{{ learningStats?.streakDays || 0 }}</div>
              <div class="text-sm text-gray-500">Дней подряд</div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Navigation Tabs -->
    <div class="bg-white border-b">
      <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <nav class="flex space-x-8">
          <router-link
            to="/profile"
            exact-active-class="border-blue-500 text-blue-600"
            class="border-b-2 border-transparent py-4 px-1 text-sm font-medium text-gray-500 hover:text-gray-700 hover:border-gray-300"
          >
            Обзор
          </router-link>
          <router-link
            to="/profile/courses"
            active-class="border-blue-500 text-blue-600"
            class="border-b-2 border-transparent py-4 px-1 text-sm font-medium text-gray-500 hover:text-gray-700 hover:border-gray-300"
          >
            Мои курсы
          </router-link>
          <router-link
            to="/profile/settings"
            active-class="border-blue-500 text-blue-600"
            class="border-b-2 border-transparent py-4 px-1 text-sm font-medium text-gray-500 hover:text-gray-700 hover:border-gray-300"
          >
            Настройки
          </router-link>
        </nav>
      </div>
    </div>

    <!-- Profile Content -->
    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <router-view />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue';
import { useAuthStore } from '@/stores/auth';
import { progressApi } from '@/services/api';
import { mockLearningStats, mockApiDelay } from '@/services/mockData';
import type { LearningStats } from '@/types';

const authStore = useAuthStore();
const learningStats = ref<LearningStats | null>(null);

const defaultAvatar = 'https://ui-avatars.com/api/?name=' + encodeURIComponent(authStore.user?.fullName || 'Пользователь') + '&background=3B82F6&color=fff';

const formatDate = (dateString: string | undefined) => {
  if (!dateString) return '';
  const date = new Date(dateString);
  return date.toLocaleDateString('ru-RU', { year: 'numeric', month: 'long' });
};

onMounted(async () => {
  try {
    // Mock API call with delay
    await mockApiDelay(500);
    learningStats.value = mockLearningStats;
  } catch (error) {
    console.error('Failed to load learning stats:', error);
  }
});
</script>
